header{
    width:100%; 
    background: #00df70;
    .top{
        position: relative;
        top: 0;
        width: 100%;
        height: 28px;
        margin: 0 auto;
        background: #fefefc;
        display: flex;
        align-items: center;
        >.inner{
           width: 100%;
           padding: 5%;
        }
        .sns{
            margin-right: auto;
            display:flex;
            justify-content: right;
            align-items: center;
            li{
                margin-right: 12px;
                a{
                    font: 12px/28px 'Open Sans';
                    color: #393834;
                }
            }
        }
        .util{
            margin-left: auto;
            display:flex;
            justify-content: right;
            align-items: center;
            li{
                margin-left: 20px;
                a{
                    font: 10px/38px 'Open Sans';
                    color: #203850;
                }
            }
        }
    }
    .inner{
       width:100%; 
       height:80px; 
       position:relative; 
       display: flex;
       align-items: center;
       justify-content: left;
       >h1{
          width: 20%;
          font-size: 0;
          background: #fffffe;
          margin-right: 100px;
          a{
             display: block;
             text-align: center;
             font:bold 30px/80px 'Open Sans'; 
             color:#00df70; 
          }
       }
       .menuWeb{
          width: 70%;
          
          #gnb{
             width: 100%;
             height: 80px;
             display: flex;
             align-items: center;
             justify-content: space-between;
             li{
                a{
                   display:block; 
                   font:bold 14px/80px 'Open Sans'; 
                   color:#fffffe; 
                }
             }
          }
       }
       
       .btnCall{
          display:none; 
          width:20px; 
          height:20px; 
          position:absolute; 
          right:30px; 
          top:50%; 
          transform:translateY(-50%); 
          &::before{
             content:''; 
             display:block; 
             width:100%; 
             height:2px; 
             background:#393834; 
             position:absolute; 
             top:0; 
             left:0; 
             transition:top 0.5s 0s, transform 0.5s 0.5s; 
          }
          &::after{
             content:''; 
             display:block; 
             width:100%; 
             height:2px; 
             background:#393834; 
             position:absolute; 
             bottom:0; 
             left:0; 
             transition:bottom 0.5s 0s, transform 0.5s 0.5s; 
          }
          span{
             display:block; 
             width:100%; 
             height:2px; 
             background:#393834; 
             position:absolute; 
             top:50%; 
             margin-top:-1px; 
             left:0; 
             text-indent:-9999px; 
             transition:0.5s; 
          }
          &.on{
             &::before{
                top:50%; margin-top:-1px; 
                transform:rotate(45deg); 
             }
             &::after{
                bottom:50%; margin-bottom:-1px; 
                transform:rotate(-45deg); 
             }
             span{
                transform:translateX(100%); 
                opacity:0; 
             }
          }
       }
       .menuMo{
          position:fixed; 
          top:0; 
          left:-270px; 
          width:270px;
          height:100vh;
          background:#fafafa; 
          transition:0.5s; 
          z-index:10; 
          box-shadow: 0px 3px 5px rgba(0,0,0,0.3) ;
 
          h1{
             padding: 25px 20px 26px 20px;
             a{
                display:block; 
                font:bold 24px/1 'Open Sans'; 
                color:#393834; 
             }
             span{
                display:block;
                font-size:12px; color:#aaa;
             }
          }
          .moMenuList{
             position: relative;
             .login{
                width: 100%;
                background: rgba(0,0,0,0.07);
                display: flex;
                align-items: center;
                border-top:1px solid #bbb; 
                padding: 25px 20px;
                >a{
                   display: block;
                   width: 50px;
                   height: 50px;
                   background: #c6aa92;
                   color: #fafafa;
                   text-align: center;
                   border-radius: 50%;
                   font: 25px/50px 'Open Sans';
                   margin-right: 20px;
                }
                .utilMo{
                   display: flex;
                   align-items: center;
                   justify-content: space-between;
                   li{
                      display: inline-block;
                      a{
                         display:block; 
                         font: 16px/1 'Open Sans'; 
                         color:#aaa; 
                      }
                      &:nth-of-type(1){
                         position: relative;
                         width: 70px;
                         &::after{
                            content: '|';
                            display:block;
                            font: 16px/1 'Open Sans'; 
                            color:#aaa; 
                            position: absolute;
                            top: 0;
                            right: 12px;
                         }
                      } 
                   }
                }
                
             }
             #gnbMo{
             
                li {
                   &:last-child a{
                      border-bottom: 1px solid #bbb;
                   }
                   a{
                   display:block; 
                   width:100%; 
                   padding:16px 0px; 
                   border-top:1px solid #bbb; 
                   font: 14px/1 "Open Sans"; 
                   color:#aaa; 
                   text-align:center; 
                   }
                   &:hover{
                      a{
                         color:#c6aa92;
                         background: rgba(0,0,0,0.03);
                      }
                   }
                }
                
             }
             .csMo{
                display: flex;
                align-items: center;
                li{
                   &:last-child a{
                   border-right:none;
                   }
                   a{
                      display: flex;
                      flex-direction: column;
                      align-items: center;
                      justify-content: center;
                      width: 90px;
                      height: 80px;
                      font: 14px/1 "Open Sans"; 
                      color:#aaa; 
                      text-align:center; 
                      border-right: 1px solid #bbb;
                      border-bottom: 1px solid #bbb;
                      i{
                         font-size: 25px;
                         margin-bottom: 8px;
                      }
                   }
                   &:hover{
                      a{
                         color:#c6aa92;
                         background: rgba(0,0,0,0.03);
                      }
                   }
                }
             }
          }
          &.on{
             left:0px;
          }
          .follow{
             width: 270px;
             margin-top: 40px;
             h3{
                font:bold 14px/1 "Open Sans"; 
                color:#888; 
                text-align:center; 
                margin-bottom: 20px;
             }
             .snsMo{
                bottom: 0;
                display: flex;
                align-items: center;
                justify-content: space-around;
                li{
                   a{
                      display: block;
                      width: 50px;
                      height: 50px;
                      border-radius: 50%;
                      font:bold 20px/50px "Open Sans"; 
                      color:#fafafa; 
                      text-align: center;
                      background:rgba(0,0,0,0.3);;
                   }
                }
             }
          }
          
       }
    }
 }

 @media screen and (min-width:1179px){
    header{
       .inner{
         padding-right:20%;
       }
    }
    .visual .imgBox{
      margin-left:-20%;
    }
 }